import {
	uniSegmentedControl
} from "@dcloudio/uni-ui";
export default {
	components: {
		uniSegmentedControl,
	},
	onLoad(options) {
		this.id = options.id
		this.getPageData()
	},
	data() {
		return {
			items: [{
				title: '最新',
				type: "new"
			}, {
				title: '热门',
				type: "hot"
			}],
			current: 0,
			listData: [],
			id: 0,
			requestParams: {
				limit: 30,
				skip: 0,
				order: "new"
			}
		}
	},
	methods: {
		onClickItem(e) {
			if (this.current !== e.currentIndex) {
				this.current = e.currentIndex;
			} else {
				return;
			}
			this.requestParams.skip = 0
			this.requestParams.order = this.items[this.current].type
			this.listData = []
			this.getPageData()
		},
		getPageData() {
			this.request({
				url: "http://157.122.54.189:9088/image/v1/vertical/category/" + this.id + "/vertical",
				data: this.requestParams
			}).then(result => {
				this.listData = [...this.listData, ...result.res.vertical]
			})
		},
		handleScrolltolower() {
			this.requestParams.skip += this.requestParams.limit
			this.getPageData()
		},
		//点击跳转图片详情
		handleGoToImgDetail(imgList, imgIndex) {
			getApp().globalData.imgList = imgList
			getApp().globalData.imgIndex = imgIndex
			uni.navigateTo({
				url: "/pages/img-detail/img-detail"
			})
		}
	}
}
